@font-face {
  font-family: "light";
  src: url("../../assets/fonts/Montserrat-Light.ttf");
}
@font-face {
  font-family: "regular";
  src: url("../../assets/fonts/Montserrat-Regular.ttf");
}
@font-face {
  font-family: "medium";
  src: url("../../assets/fonts/Montserrat-Medium.ttf");
}
@font-face {
  font-family: "semibold";
  src: url("../../assets/fonts/Montserrat-Semibold.ttf");
}
@font-face {
  font-family: "bold";
  src: url("../../assets/fonts/Montserrat-Bold.ttf");
}
@font-face {
  font-family: "extrabold";
  src: url("../../assets/fonts/Montserrat-ExtraBold.ttf");
}
@font-face {
  font-family: "thin";
  src: url("../../assets/fonts/Montserrat-Thin.ttf");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

.container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 15px;
}

.section-title {
  font-family: semibold;
  font-size: 40px;
  margin-bottom: 51px;
  position: relative;
}
.section-title::after {
  position: absolute;
  content: "";
  left: 0;
  top: -11px;
  width: 74px;
  height: 5px;
  background-color: #E33E54;
}

.body {
  font-size: 16px;
}

@import-normalize;
.header__container {
  max-width: 1785px;
  margin: 0 auto;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 26px 0;
  margin-bottom: 30px;
}
.header__logo {
  width: 100px;
  height: 100px;
}
.header__burger {
  width: 56px;
  height: 56px;
  background: transparent;
  border-radius: 50%;
  border: 1px solid #4c858b;
  cursor: pointer;
}
.header__burger-line {
  position: relative;
  margin: auto;
  width: 22px;
  height: 1.83px;
  border-radius: 5px;
  border: none;
  background-color: #4c858b;
}
.header__burger-line::before {
  position: absolute;
  content: "";
  width: 22px;
  height: 1.83px;
  border-radius: 5px;
  background-color: #4c858b;
  bottom: 7.33px;
  left: 0;
}
.header__burger-line::after {
  position: absolute;
  content: "";
  width: 22px;
  height: 1.83px;
  border-radius: 5px;
  background-color: #4c858b;
  top: 7.33px;
  left: 0;
}
.header__navigation-link {
  text-decoration: none;
}
.header__navigation-list {
  padding: 0;
  display: flex;
  gap: 37px;
  justify-content: space-between;
}
.header__navigation-item {
  color: #4c858b;
  font-family: "bold", sans-serif;
  list-style-type: none;
}
.header__phone-number {
  font-family: "light", sans-serif;
  flex-basis: 161px;
}
.header__phone-link {
  text-decoration: none;
  color: inherit;
}
.header__phone-number--bold {
  font-family: "semiBold", sans-serif;
}
.header__call-me {
  flex-basis: 156px;
}
.header__call-me-text {
  font-family: bold;
  color: #AE2236;
  border-bottom: 1px dotted;
  margin: 0;
  padding-bottom: 5px;
}
.header__social {
  display: flex;
  justify-content: space-between;
  flex-basis: 115px;
}
.header__social-link {
  text-decoration: none;
}
.header__shopping-group {
  flex-basis: 202px;
}
.header__shopping-button {
  position: relative;
  border: 1px solid #E5E5E5;
  background-color: transparent;
  border-radius: 50%;
  width: 56px;
  height: 56px;
}
.header__shopping-button::after {
  font-size: 11px;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  content: "10";
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  width: 21px;
  height: 21px;
  background-color: #E33E54;
}

.social {
  display: flex;
  justify-content: space-between;
  flex-basis: 115px;
}
.social__link {
  text-decoration: none;
}

.footer {
  background-color: #0C2F33;
}
.footer__inner {
  padding: 90px 0;
  columns: 3;
}
.footer__title {
  font-family: bold;
  font-size: 22px;
  color: #FFFFFF;
  text-transform: uppercase;
  margin-bottom: 18px;
}
.footer__list {
  columns: 2;
  list-style: none;
  padding: 0;
}
.footer__catalog-item {
  font-family: regular;
  font-size: 14px;
  color: #FFFFFF;
  margin-bottom: 19px;
}
.footer__link {
  text-decoration: none;
  color: #FFFFFF;
}
.footer__worktime-schedule {
  display: flex;
  margin-bottom: 8px;
}
.footer__worktime-clock {
  margin-right: 8px;
}
.footer__worktime-text--left {
  margin-right: 12px;
}
.footer__worktime-text--accent {
  font-family: regular;
  font-size: 14px;
  color: #FFFFFF;
}
.footer__worktime-text {
  color: #4C858B;
  font-family: regular;
  font-size: 14px;
}
.footer__worktime-text--adress {
  margin-bottom: 25px;
}
.footer__worktime-social svg {
  margin-right: 36px;
}
.footer__worktime-adress {
  display: flex;
}
.footer__worktime-placeholder {
  margin-right: 7px;
}
.footer__title--worktime {
  margin-bottom: 16px;
}

.breadcrumbs {
  margin-bottom: 47px;
}
.breadcrumbs__title {
  font-family: "bold";
  font-size: 60px;
  margin-bottom: 41px;
}
.breadcrumbs__home {
  border-top: 1px solid #E5E5E5;
  border-bottom: 1px solid #E5E5E5;
  padding: 16px 0;
  margin-bottom: 81px;
}
.breadcrumbs__home-link {
  position: relative;
  text-decoration: none;
  font-family: regular;
  color: #b1b1b1;
  font-size: 12px;
  margin-right: 20px;
}
.breadcrumbs__home-link::after {
  padding: 2px;
  position: absolute;
  top: 6px;
  left: 54px;
  content: "";
  border: solid #4c858b;
  border-width: 0 1px 1px 0;
  transform: rotate(-45deg);
  border-radius: 0.5px;
}
.breadcrumbs__home-location {
  font-size: 12px;
  font-family: regular;
  color: #4c858b;
}
.breadcrumbs__text {
  font-family: regular;
  font-size: 16px;
  line-height: 30px;
}
.breadcrumbs__text--accent {
  color: #AE2236;
}

.banner__container {
  max-width: 1220px;
}
.banner__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 64.46px;
}
.banner__text-wrapper {
  line-height: 63px;
}
.banner__title-wrapper {
  margin-bottom: 39px;
}
.banner__title-medium {
  font-family: semibold, sans-serif;
  font-size: 40px;
}
.banner__title-big {
  font-family: extrabold;
  font-size: 50px;
  color: #E33E54;
}
.banner__advantage-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 360px;
}
.banner__advantage-item {
  font-family: regular;
  font-size: 16px;
  line-height: 24px;
}
.banner__advantage-item::marker {
  color: #EA6628;
  font-size: 150%;
  font-family: bold;
  vertical-align: middle;
  list-style-position: inside;
}
.banner__image-wrapper {
  display: relative;
}
.banner__image {
  display: absolute;
}

.mounting {
  counter-reset: section;
  margin-bottom: 88px;
}
.mounting__separator {
  margin-bottom: 11px;
}
.mounting__title {
  font-family: semibold;
  font-size: 40px;
  margin-bottom: 51px;
}
.mounting__services {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 278px;
}
.mounting__services-item {
  display: flex;
  align-items: center;
}
.mounting__services-number {
  font-family: bold;
  font-size: 70px;
  margin-right: 42px;
  width: 45px;
}
.mounting__services-number::before {
  counter-increment: section;
  content: counter(section);
}
.mounting__services-description {
  font-family: regular;
  font-size: 16px;
  max-width: 500px;
}

.why {
  margin-bottom: 108px;
}
.why__separator {
  margin-bottom: 11px;
}
.why__title {
  font-size: 40px;
  font-family: semibold;
  margin-bottom: 50px;
}
.why__text {
  font-size: 16px;
  font-family: regular;
  margin-bottom: 50px;
}
.why__advantages {
  display: flex;
  justify-content: space-between;
  margin-bottom: 98px;
}
.why__advantages-card {
  display: flex;
  flex-direction: column;
  flex-basis: 386px;
}
.why__advantages-card svg {
  margin-bottom: 32px;
}
.why__advantages-card-title {
  font-family: bold;
  font-size: 20px;
  margin-bottom: 10px;
}
.why__advantages-card-text {
  font-family: regular;
  line-height: 30px;
}
.why__advantages-card-text-accent {
  color: #E33E54;
}
.why__consultation-title {
  font-size: 50px;
  font-family: extraBold;
  text-align: center;
  margin-bottom: 29px;
}
.why__consultation-action-call {
  font-size: 22px;
  font-family: light;
  text-align: center;
  margin-bottom: 46px;
}
.why__consultation-social-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 39px;
}
.why__consultation-social {
  width: 203px;
  display: flex;
  justify-content: space-between;
}
.why__consultation-social svg path {
  fill: #EA6628;
}
.why__consultation-form-wrapper {
  display: flex;
  justify-content: center;
}
.why__consultation-form-inner {
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.15);
  padding: 57px 55px 61px 59px;
}
.why__consultation-form-title {
  font-size: 20px;
  font-family: medium;
  margin-bottom: 12px;
}
.why__consultation-form-text {
  font-family: regular;
  font-size: 16px;
  margin-bottom: 22px;
}
.why__consultation-form-title--accent {
  color: #EA6628;
}
.why__consultation-form-field {
  width: 240px;
  height: 56px;
  margin-right: 16px;
}
.why__consultation-form-field::placeholder {
  font-family: medium;
  font-size: 14px;
  padding-left: 17px;
}
.why__consultation-form-button {
  font-family: bold;
  font-size: 16px;
  width: 240px;
  height: 56px;
  color: #FFFFFF;
  background-color: #215359;
  border-radius: 4px;
  border: none;
}

.video {
  margin-bottom: 99px;
}
.video__separator {
  margin-bottom: 11px;
}
.video__title {
  font-family: semibold;
  font-size: 40px;
  margin-bottom: 66px;
}
.video__text {
  font-family: regular;
  font-size: 16px;
  margin-bottom: 51px;
}
.video__image {
  max-width: 1156px;
  height: auto;
}

.samples {
  margin-bottom: 136px;
}
.samples__separator {
  margin-bottom: 11px;
}
.samples__title {
  font-family: semibold;
  font-size: 40px;
  margin-bottom: 66px;
}
.samples__text {
  font-family: regular;
  font-size: 16px;
  margin-bottom: 48px;
}
.samples__photos {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.samples__item {
  flex-shrink: 1;
  flex-grow: 1;
}
.samples__item-image {
  max-width: 100%;
  display: block;
}