.footer
    background-color: #0C2F33
    &__inner
        padding: 90px 0
        columns: 3 
    &__title
        font-family: bold
        font-size: 22px
        color: #FFFFFF
        text-transform: uppercase
        margin-bottom: 18px
    &__list
        columns: 2
        list-style: none
        padding: 0
    &__catalog
        // margin-right: 105px
    &__catalog-item
        font-family: regular
        font-size: 14px
        color: #FFFFFF
        margin-bottom: 19px
    &__link
        text-decoration: none
        color: #FFFFFF

  
    &__worktime-schedule
        display: flex
        margin-bottom: 8px    
    &__worktime-clock
        margin-right: 8px
    &__worktime-text--left
        margin-right: 12px
    &__worktime-text--accent
        font-family: regular
        font-size: 14px
        color: #FFFFFF
    &__worktime-text
        color: #4C858B
        font-family: regular
        font-size: 14px
    &__worktime-text--adress
        margin-bottom: 25px
    &__worktime-social svg
        margin-right: 36px
    
    &__worktime-adress
        display: flex
    &__worktime-placeholder
        margin-right: 7px
    
    &__title--worktime 
        margin-bottom: 16px
